@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100vw;
  height: 100vh;
  background: url(/static/media/background.7f2638e0.jpg) no-repeat;
  background-size: cover;
  background-position: top right;
}

.text-fields {
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.double-text-field {
  width: 100%;
  display: grid;
  grid-template-columns: calc(50% - 5px) calc(50% - 5px);
  grid-column-gap: 10px;
  max-width: 500px;
}

.big-logo {
  width: 235px;
  position: absolute;
  top: 50px;
  left: 50px;
  z-index: 3;
}

.form-section {
  background: white;
  border-radius: 0px 50px 50px 0px;
  width: 65%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 0;
  bottom: 0;
  overflow-y: auto;
  overflow-x: hidden;
}

@media (min-width: 768px) and (max-width: 1024px) {
  .text-fields {
    width: 400px;
  }
}

@media (max-width: 767px) {
  .form-section {
    width: 100%;
    height: 80%;
    border-radius: 25px 25px 0px 0px;
    justify-content: flex-start;
  }
  .text-fields {
    width: 90%;
  }

  .double-text-field {
    width: 85vw !important;
  }
}

.lottie-container {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

input {
  width: 100%;
  height: 45px;
  border: 1px solid rgba(213, 213, 213, 1);
  box-sizing: border-box;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px 20px 10px 20px;
  outline: none;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  color: #000000;
  transition: all 0.3s ease-in-out;
  margin-top: 20px;
  max-width: 500px;
}

input:-ms-input-placeholder {
  box-shadow: none;
  background: #e8e8e8;
  border: 1px solid rgba(213, 213, 213, 0);
  color: #9a9a9a;
}

input:placeholder-shown {
  box-shadow: none;
  background: #e8e8e8;
  border: 1px solid rgba(213, 213, 213, 0);
  color: #9a9a9a;
}

@media (max-width: 767px) {
  .short-version {
    width: 85vw !important;
  }
}

select {
  width: 100%;
  height: 45px;
  border: 1px solid rgba(213, 213, 213, 1);
  box-sizing: border-box;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px 20px 10px 20px;
  outline: none;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  color: #000000;
  transition: all 0.3s ease-in-out;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  background: none;
}

.select-container {
  margin-top: 20px;
  position: relative;
}

select:hover {
  cursor: pointer;
}

.icon {
  color: black;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  right: 10px;
  pointer-events: none;
}

.collapsed select {
  border: 1px solid rgba(213, 213, 213, 0);
  box-shadow: none;
  color: #9a9a9a;
}

.collapsed {
  background: #e8e8e8;
  border-radius: 10px;
}

.availableOption {
  color: black;
}

.primary-button {
  margin-top: 20px;
  width: 100%;
  height: 45px;
  border: none;
  background: #ff5a79;
  border-radius: 10px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #ffffff;
  text-transform: uppercase;
  max-width: 500px;
  z-index: 5;
}

.primary-button:hover {
  cursor: pointer;
}

@media (max-width: 767px) {
  .primary-button {
    width: 85vw;
  }
}

.disabled-button {
  pointer-events: none;
  opacity: 0.5;
}

.disabled-button:hover {
  cursor: default;
}

.navbar {
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  background: white;
  z-index: 3;
}

.small-logo {
  width: 150px;
  padding: 20px;
}

.question-container {
  width: min(500px, 100%);
  display: grid;
  grid-template-rows: 20% 80%;
  grid-template-columns: 100%;
  justify-items: center;
  max-height: 80%;
}

.question-label {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: none;
}

.answers {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 5px;
  justify-items: center;
  overflow-y: auto;
}

@media (max-width: 767px) {
  .answers {
    grid-template-columns: repeat(2, 1fr);
  }

  .question-container {
    margin-top: 20px;
  }
}

.option-container {
  position: relative;
  background: white;
  width: 150px;
  height: 85px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0px 10px 0px;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1);
}

.success-icon {
  position: absolute;
  top: 10px;
  left: 10px;
  color: black;
}

.active .success-icon {
  color: green;
}

.active {
  border: 2px solid green;
  box-shadow: 0px 0px 5px 1px rgba(0, 255, 0, 0.1);
}

.option-container p {
  text-align: center;
  width: 60%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: none;
}

.option-container:hover {
  cursor: pointer;
}

.success-container {
  display: grid;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: min(500px, 100%);
  text-align: center;
}

.animation-div {
  width: 100%;
}

